            <p class="hp-p1-body mb-0">You can see latest user registered at here</p>
          </b-col>
          <div class="hp-header-search-item position-relative">
            <b-form-input placeholder="Search email here..." autocomplete="off" v-model="email" type="email" @keyup.enter="findUser" />
          </div>
          <b-col class="hp-flex-none w-auto">
            <b-form-select v-model="selected" :options="options"></b-form-select>
          </b-col>
        </b-row>
