        <b-alert variant="danger" v-if="errorfind" show>email must be valid</b-alert>
      </b-col>
    </b-row>
    <div v-if="userData.list_data.length === 0">No Data To Display</div>
    <div v-else>
      <b-table-simple responsive borderless class="mb-0">
        <b-thead>
          <b-tr>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> # </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> email </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> TYpe </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> status </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> registered_at </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> details </span>
            </b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr :key="index" v-for="(item, index) in userData.list_data">
            <b-td>
              <span class="mb-0 font-weight-medium text-black-100 hp-text-color-dark-0">
                {{ item.id }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ item.email }}
              </span>
            </b-td>

            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ item.role }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ item.state }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ new Date(item.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}
              </span>
            </b-td>
